import { IMessage, IMessageTranslator } from './MessageMenager';

// -------------------- APP status
export interface IAppStatus {
    isLoged: boolean,
    user: IUser | null,
    userToken: IUserToken | null,
    messages: IMessage[],
    messagesTranslators: IMessageTranslator[],
    dialog: React.ReactNode,
}

// -------------------- Commonly used interfaces

export interface IUser {
    username: string;
    characters: ICharacterBrief[];
}
export interface IUserToken {
    username: string;
    token: string;
}
export interface IActionToken {
    heroID: number;
    token: string;
}
export interface IAction {
    type: string;
    payload: any;
}
export interface ICharacterBrief {
    name: string;
    nickname: string;
    level: number;
    orders: Orders;
}

export enum Orders {
    none,
    Windrunners,
    Skybreakers,
    Dustbringers,
    Edgedancers,
    Truthwatchers,
    Lightweavers,
    Elsecallers,
    Willshapers,
    Stonewards,
    Bondsmiths,
}

export interface IPassedData<T> {
    UserToken: IUserToken;
    Data: T;
}

// ------------------ interfaces passed by payload

export interface ILoginData {
    user: IUser,
    userToken: IUserToken,
}


// ------------------

export default IAppStatus;


