import * as React from 'react';

import Popover from '@material-ui/core/Popover';

// -------------------- images
import * as invalidSrc from '../img/Login/invalid.png';


export default class ValidationImg extends React.Component<{ name: string, description: string[] }, { clicked: boolean, img: JSX.Element }>{
    constructor(props: any) {
        super(props);

        this.handleClick = this.handleClick.bind(this);

        this.state = {
            clicked: false,
            img: (<img src={String(invalidSrc)} className="ValidationImg" onClick={this.handleClick} id={this.props.name} />),
        };

    }
    public render(): JSX.Element {
        return (
            <span>
                {this.state.img}
                <Popover
                    open={this.state.clicked}
                    anchorEl={document.getElementById(this.props.name) as HTMLElement}
                    anchorOrigin={{ horizontal: 'left', vertical: 'bottom' }}
                    transformOrigin={{ horizontal: 'left', vertical: 'top' }}
                    onMouseDown={this.handleClick}
                    className="ValidationPopup"
                >{this.props.description.map((e) => (<p key={e}>{e}</p>))}</Popover>
            </span>
        )
    }
    private handleClick(): void {
        if (!this.state.clicked) {
            setTimeout(() => { if (this.state.clicked) { this.setState({ clicked: false }); } }, 3000);
        }
        this.setState({ clicked: !this.state.clicked });
    }
}


