import axios from 'axios';
import * as React from 'react';
import { connect } from 'react-redux';

import Button from '@material-ui/core/Button';

import '../../css/dialogs/RemoveCharacterDialog.css';

import { Close_Dialog, Pop_Messages, Remove_Hero, } from '../../actions/actionCreators';
import { IMessage, IMessageTranslator, MessageType } from '../../MessageMenager';
import { IStringValidation } from '../../Registry';
import { IAppStatus, ICharacterBrief, IPassedData, IUserToken, } from '../../TYPES';

import { Divider, TextField, } from '@material-ui/core';
import ValidationImg from '../ValidationImg';

// -------------------- images
import * as warriorSrc from '../../img/Account/warrioricon.png';
import * as validSrc from '../../img/Login/valid.png';

// ----------- constants
import { localhost } from '../../data/connectionConf';


class ConnectedRemoveCharacterDialog extends React.Component<{ userToken: IUserToken, closedialogFun: VoidFunction, popmessFun: any, removeHeroFromList: (heroname: string) => void, characters: ICharacterBrief[] }, { inputs: IRemoveCharacter, chosenHero: number }>{
    private KnownMessages: IMessageTranslator[];
    constructor(props: any) {
        super(props);
        // -------binding
        this.handleUserInput = this.handleUserInput.bind(this);
        this.renderInput = this.renderInput.bind(this);
        this.RemoveHero = this.RemoveHero.bind(this);
        this.handleHerosChoise = this.handleHerosChoise.bind(this);

        this.KnownMessages = [
            {
                actions: [(e: IMessage) => {
                    const failed = this.state.inputs;
                    failed.password.description.push(e.description);
                    failed.password.text = '';
                    failed.password.isValid = 1;
                    this.setState({ inputs: failed, });
                }],
                images: [],
                ispopup: false,
                name: "passwordErr",
                title: "Password is incorrect.",
                type: MessageType.ERROR,
            } as IMessageTranslator,
        ]

        this.state = {
            chosenHero: -1,
            inputs: {
                password: {
                    description: [],
                    isValid: -1,
                    text: "",
                    validation: (data: IStringValidation) => {
                        const failed = this.state.inputs;
                        failed.password.isValid = -1;
                        this.setState({ inputs: failed, });
                        return true;
                    }
                } as IStringValidation,
            } as IRemoveCharacter,
        }

    }

    public render() {
        return (<div className="dialog">
            <div className="RemoveCharacterDialog">
                <div className="RemoveCharacterForm">
                    <img src={String(warriorSrc)} />
                    <div className="TitleBar">Choose Character to remove</div>
                    <Divider />
                    <div className="HerosList">
                        {
                            this.props.characters.map((e, index) => {
                                const fun = () => { this.handleHerosChoise(index); };
                                return (
                                    <div className={(this.state.chosenHero === index) ? "HeroActive HeroDisplay" : "HeroDisplay"} key={index} onClick={fun}>
                                        <div className="TableCell"><b>{e.name}</b> <i>{e.nickname}</i></div>
                                        <div className="TableCell">Level:</div>
                                        <div className="TableCell">{e.level}</div>
                                    </div>
                                );
                            })
                        }
                    </div>
                    <div className="TitleBar">Confirm with password</div>
                    <Divider />
                    {this.renderInput("password", "password", "Password")}
                    <button disabled={this.state.chosenHero === -1} onClick={this.RemoveHero}>Remove the character</button>
                </div>
            </div>
            <div className="dialogBottom">
                <Button
                    variant="flat"
                    color="primary"
                    onClick={this.props.closedialogFun}
                >
                    Abord</Button>
            </div>
        </div>);
    }
    private renderInput(type: string, name: string, placeholder: string = ""): JSX.Element {
        const element = (<TextField
            id={name + "-input"}
            label={placeholder}
            type={type}
            name={name}
            value={this.state.inputs[name].text} onChange={this.handleUserInput}
        />);
        let ret = (<div>{element}</div>);
        switch (this.state.inputs[name].isValid) {
            case -1:
                return ret;
            case 0:
                ret = (<div className="Validated">{element} <img src={String(validSrc)} className="ValidationImg" /></div>);
                break;
            case 1:
                ret = (<div className="Validated">{element}<ValidationImg name={name + "-validationImg"} description={this.state.inputs[name].description} /></div>);
                break;
        }
        return ret;
    }
    private handleUserInput(e: React.FormEvent<HTMLInputElement>) {
        const name = e.currentTarget.name as string;
        const value = e.currentTarget.value as string;
        const inp = this.state.inputs;
        inp[name].text = value;
        if (value === "") {
            inp[name].isValid = -1;
        }
        else {
            inp[name].validation(inp[name]);
        }
        this.setState({ inputs: inp });
    }
    private handleHerosChoise(index: number) {
        this.setState({ chosenHero: index });
    }
    private RemoveHero() {
        if (this.state.chosenHero < 0 || this.state.chosenHero >= this.props.characters.length) {
            return;
        }
        const passeddata: IPassedRemoveCharacter = {
            HeroName: this.props.characters[this.state.chosenHero].name,
            Password: this.state.inputs.password.text,
        };
        axios.post(localhost + `/api/HerosRemove`, { UserToken: this.props.userToken, Data: passeddata } as IPassedData<IPassedRemoveCharacter>)
            .then(res => {
                this.props.popmessFun([{ title: "removeHeroSucc", description: res.data.removedHero + " has been successfully removed.", } as IMessage], this.KnownMessages);
                this.props.removeHeroFromList(res.data.removedHero);
                this.props.closedialogFun();
            })
            .catch(error => {
                if (error.response === undefined) {
                    this.props.popmessFun([{ title: "serverErr", description: "I've got bad feelings about this...", } as IMessage], this.KnownMessages);
                } else {
                    this.props.popmessFun([{ title: error.response.data.type, description: error.response.data.description } as IMessage], this.KnownMessages);
                }
            });
    }
}

// ----------- connect to store

const mapDispatchToProps = (dispatch: any) => {
    return {
        closedialogFun: () => dispatch(Close_Dialog()),
        popmessFun: (message: IMessage[], translators: IMessageTranslator[]) => dispatch(Pop_Messages(message, translators)),
        removeHeroFromList: (heroname:string) => dispatch(Remove_Hero(heroname)),
    };
};
const mapStateToProps = (state: IAppStatus) => {
    if (state.user === null) {
        throw Error("No user in storage");
    }
    return {
        characters: state.user.characters,
        userToken: state.userToken,
    }
};

const RemoveCharacterDialog = connect(mapStateToProps, mapDispatchToProps)(ConnectedRemoveCharacterDialog);

// --------------- interfaces
interface IRemoveCharacter {
    password: IStringValidation;
}
interface IPassedRemoveCharacter {
    Password: string;
    HeroName: string;
}

// ===============================


export default RemoveCharacterDialog; 


